<template>
  <div id="tabbar" class="tabbar" ref="bar">
    <div class="tabbar-item" v-for="(item, idx) in tabbar" :key="idx"
         :class="idx===selIdx ? 'selected' : ''"
         :style="{color: idx===selIdx ? tabColor : '', 'border-bottom': idx===selIdx ? bottomHeight + 'px solid' : ''}"
         @click="selectItem(idx)">{{item}}</div>
    <!--æå°¾å·´çææä½¿ç¨çæ¯css3çtransitionçease-->
    <div class="tarbar-bottom" :style="{width: itmW + 'px', height: bottomHeight + 'px', 'background-color': tabColor, left: selIdx * itmW + 'px'}"></div>
  </div>
</template>
<script>
  export default {
    props: {
      tabbar: { // itemçé¡¹ç®å
        type: Array,
        default: function () {
          return []
        }
      },
      tabColor: { // å¸ææ¾ç¤ºçé¢è² é»è®¤ç»¿è²
        type: String,
        default: 'green'
      },
      callback: { // ç¹å»itemä¹åçåè°
        type: Function,
        default: function () {
          return function () {}
        }
      },
      bottomHeight: { // åºé¨æ¡çé«åº¦
        type: [Number, String],
        default: 1
      }
    },
    data () {
      return {
        selIdx: 0, // ç¹å»tab
        itmW: (100 / this.tabbar.length) + '%'  // æ¯ä¸ªäººitemçå®½åº¦
      }
    },
    mounted () {
      const tabW = this.$refs.bar.offsetWidth
      this.itmW = tabW / this.tabbar.length
    },
    methods: {
      selectItem (idx) {
        this.selIdx = idx
        this.callback(idx)
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "../static/iconfont.scss";
  @import "../static/color.scss";
  .tabbar {
    display: flex;
    flex-direction: row;
    background-color: $white;
    position: relative;
    width: 100%;
  }
  .tabbar-item {
    flex: 1;
    height: 40px;
    line-height: 40px;
    color: $color3;
    text-align: center;
  }
  .tabbar-item.selected {
    color: $success;
    border-bottom: 1px solid;
  }
  .tarbar-bottom {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 1px;
    transition: all ease-in-out .3s;
  }
</style>



